.header {
    background: none;
    padding: 50px;
    width: 100%;
  }

.icon {
    font-size: 40px;
    position: fixed;
    z-index: 1;

}

.icon:active,.icon:hover {
    font-size: 40px;
    color: #b3b3b3;
}

.sidenav {
    width: 300px;
    background: #eee;
    position: fixed;
    height: 100vh;
    padding: 50px;
    z-index: 0;
    left: -100%;
    transition: all 2s;
}
.sidenav.active {
    left:0;

}

ul {
    padding: 0;
}

ul li {
    list-style-type: none;
    font-size: 25px;
    margin: 10px 0;
}

ul li a {
    text-decoration: none;
    color: black;
    display: block;
    padding: 20px 20px;
    border-radius: 10px;
}

ul li a:hover,
ul li a:active {
    background-color: #cccccc;
    font-weight: bolder;
    transition: background-color 0.8s;

}

.blog-name {
    display: flex;
    color: black;
    margin-bottom: 40px;
    justify-content: center;
    font-weight: bold;
    font-size: 82px;
    box-sizing: border-box;
}

.navbar {
    width: 100%;
    padding: 40px 0px;
    text-align: center;
    
}

.navbar-menu {
    color: rgb(0, 0, 0);
    font-size: 25px;
    text-decoration: none;
    margin: 25px;
}

.navbar-menu:hover {
    color: rgba(0, 0, 0, 0.501);
    text-decoration: none;
}



